$darkCell: #272932;
$greenBoarder: #0F7173;
$lightBackground: #E7ECEF;
$bombRed: #F05D5E;
$flagYelow: #D8A47F;

.squer{
    position: absolute;  
    width: 10%;
    height: 10%;
    background-color: $darkCell;
    border: 3px solid $greenBoarder;
    outline-color: $flagYelow;
    outline-width: 1px;
    
    font-weight: bold;
    
}
.squerOpen{
    background-color: $lightBackground;
}
.squerBomb{
    background-color: $bombRed;
}
.squerFlag{
    background-color: $flagYelow;
}
.socoreLine{
    font-size: 18px;
    border-bottom: 2px solid $greenBoarder;
    margin-bottom: 10px;
    padding: 10px 10px 0 10px;
}