/* Toggle switch style*/
.switch {
    position: relative;
    display: inline-block;
    width: 34px;
    height: 14px;
    margin: 5px 20px 5px 10px;
}
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 10px;
    right: 0;
    bottom: 0;
    transition: 0.4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: -11px;
    bottom: -3px;
    transition: 0.4s;
}
input:checked + .slider {
    background-color:rgba(34, 31, 31, 0.26);
}
input:not(:checked) + .slider:before {
    background: #f05d5e;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.24);
}
input:checked + .slider:before {
    transform: translateX(26px);
}
input:not(:checked) + .slider {
    bottom: 0;
}
/* Rounded sliders */
.slider.round {
    border-radius: 34px;
    background-color: rgba(34, 31, 31, 0.26);
}
.slider.round:before {
    border-radius: 50%;
    background-color: #d8a47f;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.24);
}
.switch--box{
    display: flex;
    align-items: center;
}
